.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.header {
    background: #4caf50;
    color: #fff;
    top: 0;
    width: 100%;
    min-width: 350px;
    padding: 0.5em 0;
    position: absolute;
    
    z-index: 10;
}

.menu-icon {
    float: left;
    color: #fff;
}

.menu-icon svg {
    width: 48px;
    fill: #999999;
    cursor: pointer;
}

.header-title {
    display: inline-block;
    font-weight: 100;
    margin: 0.2em 0;
    font-size: 2em;
}

.slide-menu {
    position: fixed;
    top: 4.5em;
    padding: 1em;
    background: #4caf50;
    height: 100%;
    width: 20em;
    z-index: 10;
    /*transform: translate3d(0, 0, 0);
    transition: transform 0.2s;*/
}

.map-container {
    width: 100%;
    height: calc(100% - 55px);
}


